.container-edit-preview-article {
  &.view-edit-article {
    .container-preview-article {
      display: none;
    }
  }
}

.container-preview-article {
  background: #fff;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  padding: 1rem;
  border: 1px solid #ccc;
  word-break: break-word;
  border-radius: 0;

  h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
  }

  h2 {
    font-size: 1.75rem;
    margin-bottom: 1rem;
  }

  h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }

  h4 {
    font-size: 1.3rem;
    margin-bottom: 1rem;
  }

  h5 {
    font-size: 1.1rem;
    margin-bottom: 1rem;
  }

  p {
    margin-bottom: 1rem;
  }

  code {
    background-color: #f4f4f4;
    padding: 0.2rem 0.4rem;
    border-radius: 0.3rem;
  }

  pre {
    background-color: #f4f4f4;
    padding: 1rem;
    border-radius: 0.3rem;
    overflow-x: auto;
  }

  blockquote {
    background-color: #f4f4f4;
    padding: 1rem;
    border-left: 0.5rem solid #000;
    margin-bottom: 1rem;
  }

  ul,
  ol {
    margin-bottom: 1rem;

    li {
      margin-bottom: 0.5rem;
      list-style-type: upper-roman;
    }

    li::marker {
      color: #000;
      /* remplacer le point qui indique une liste */
    }

    ul,
    ol {
      margin-bottom: 0;
    }
  }

  table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 1rem;

    th,
    td {
      border: 1px solid #000;
      padding: 0.5rem;
    }
  }

  img {
    max-width: 100%;
    height: auto;
    margin-bottom: 1rem;
  }
}
